@use 'base/variables';
@use 'base/mixins';

.testimonial {
    background-color: variables.$verde;
    font-size: 2.4rem;
    padding: 2rem;
    color: variables.$blanco;
    border-radius: 2rem;

    blockquote{
        padding-left: 5rem;
        position: relative;
    }

    &::before {
        content: '';
        background-image: url('../img/comilla.svg');
        background-repeat: no-repeat;
        width: 6rem;
        height: 8rem;
        display: block;
        position: absolute;
    }

    p{
        text-align: right;
        color: variables.$blanco;
    }
}