@use "base/variables";
@use "base/mixins";

@include mixins.tablet {
    .seccion-inferior {
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.entrada-blog {
    margin-bottom: 2rem;
    @include mixins.tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }
    &::last-of-type{
        margin-bottom: 0;
    }
}

.informacion-meta{
    span{
        color: variables.$amarillo;
    }
}

.texto-entrada{
    a{
        color: variables.$negro;
        margin-top: 2rem;
        display: block;
        @include mixins.tablet{
            margin-top: 0;
        }
    }
    h4{
        margin: 0;
        line-height: 1.4;

        &::after {
            content: '';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: variables.$verde;
            margin-top: 1rem;
        }
    }

    span{
        color: variables.$amarillo;
    }
}
